  invalidEntry: 'Entrada invalida',
  signupor: "O",
  privacypolicy1: "1. Poltica de Privacidad de la Aplicacin",
  privacypolicy2: "VCA Per maneja la Plataforma de Gestin de Conversaciones Laraigo",
  privacypolicy3: "En esta pgina, te informamos sobre nuestras politicas relacionadas a la recoleccin, uso y manejo de la informacin personal que recibimos de los usuarios suscritos a la plataforma.",
  privacypolicy4: "Usamos tu informacin personal solo para proporcionar y mejorar la plataforma. Al usar Laraigo, usted acepta la recoleccin y uso de la informacin de acuerdo a esta poltica.",
  privacypolicy5: "2. Recoleccin y Uso de la Informacin",
  privacypolicy6: "2.1. Informacin Personal:",
  privacypolicy7: "Al usar Laraigo, se le podra solicitar que nos provea de cierta informacin personalmente identificable, la cual podra ser usada para ponernos en contacto o identificar su persona. Esta informacin incluye su correo electronico, nombre, apellido, pas, telfono y su actual posicin en su empresa. Todos los datos listados son requeridos en nuestro proceso de subscripcin.",
  privacypolicy8: "Usamos esta informacin como parte de nuestro proceso de creacin de usuario. El usuario creado puede ser identificado por la informacin ingresada y podra ser usado para contactar la persona que lo registr.",
  privacypolicy9: "2.2. Informacin Comercial:",
  privacypolicy10: "Al suscribirse a Laraigo, le solicitamos que nos provea de la siguiente informacin sobre su empresa: Nombre de empresa, tipo de industria y tamao de empresa.",
  privacypolicy11: "Usamos esta informacin como parte de nuestro proceso de creacin de usuario. Cada usuario creado en nuestro proceso de registro esta enlazado a una corporacin que contiene la informacin ingresada. Usamos la informacin para identificar al usuario y contactarlo si fuera necesario.",
  privacypolicy12: "2.3. Informacin de Facebook:",
  privacypolicy13: "En Laraigo, ofrecemos la posibilidad de enlazar tu cuenta de Facebook a una de nuestras aplicaciones en multiples procesos. En los siguientes prrafos detallamos la informacin que es compartida en cada posible enlace y la manera en la que lo usamos.",
  privacypolicy14: "2.3.1. Suscripcin:",
  privacypolicy15: "Ofrecemos la posibilidad de utilizar su cuenta de Facebook en nuestro proceso de suscripcin. Si aceptas enlazar tu cuenta de Facebook como medio de registro, recibimos el nombre de tu cuenta, correo electrnico y foto de perfil.",
  privacypolicy16: "Usamos esta informacin como parte de nuestro proceso de creacin de usuario. El usuario creado por un enlace con una cuenta de Facebook puede ser identificado por la informacin obtenida en el proceso y podra ser utilizado para contactar a la persona que lo registro si es necesario.",
  privacypolicy17: "2.3.2. Gestin de Canales:",
  privacypolicy18: "Ofrecemos la posibilidad de de enlazar pginas de Facebook a nuestras aplicaciones en nuestro modulo de gestin de canales. En el proceso de enlace, nosotros recibimos la siguiente informacin de la pgina: El ID externo y el nombre de la pgina. Adicionalmente, al enlazar una pgina de Facebook a nuestra plataforma, usted acepta el compartir con nosotros la siguiente informacin basada en eventos",
  privacypolicy19: "Posts creados por la pgina",
  privacypolicy20: "Comentarios creados por la pgina o por otros usuarios en los posts de la pgina.",
  privacypolicy21: "Mensajes enviados y recibidos mediante el Messenger de la pgina",
  privacypolicy22: "Informacin bsica del perfil de los usuarios que interactuaron con la pgina (Nombre y foto de perfil)",
  privacypolicy23: "Usamos la informacin como parte de nuestro modulo de gestin de canales. La informacin de la pgina es utilizada para crear un canal donde eventos entrantes (Posts, comentarios y mensajes) son registrados en la plataforma y procesados como tickets. Algunos usuarios selectos de la plataforma pueden enviar respuestas a estos tickets, las cuales seran mostradas como respuestas en Facebook.",
  privacypolicy24: "2.4. Informacin de Instagram:",
  privacypolicy25: "En Laraigo, ofrecemos la posibilidad de enlazar tu cuenta de Instagram a una de nuestras aplicaciones.",
  privacypolicy26: "2.4.1. Gestin de Canales:",
  privacypolicy27: "Ofrecemos la posibilidad de de enlazar pginas de Instagram a nuestras aplicaciones en nuestro modulo de gestin de canales. En el proceso de enlace, nosotros recibimos la siguiente informacin de la pgina: El ID externo y el nombre de la pgina. Nosotros tambien recibimos informacin de la cuenta de Instagram Business que realizo el enlace, incluyendo su ID. Adicionalmente, al enlazar una pgina de Instagram a nuestra plataforma, usted acepta el compartir con nosotros la siguiente informacin basada en eventos",
  privacypolicy28: "Comentarios creados por la pgina o por otros usuarios en los posts de la pgina.",
  privacypolicy29: "Mensajes enviados y recibidos mediante el Messenger de la pgina",
  privacypolicy30: "Informacin bsica del perfil de los usuarios que interactuaron con la pgina (Nombre y foto de perfil)",
  privacypolicy31: "Usamos la informacin como parte de nuestro modulo de gestin de canales. La informacin de la pgina es utilizada para crear un canal donde eventos entrantes (Comentarios y mensajes) son registrados en la plataforma y procesados como tickets. Algunos usuarios selectos de la plataforma pueden enviar respuestas a estos tickets, las cuales seran mostradas como respuestas en Instagram.",
  privacypolicy32: "3. Procesamiento de Informacin",
  privacypolicy33: "Laraigo es una plataforma de gestin de conversaciones. Su objetivo es centralizar multiples canales de redes sociales en un solo sitio web, desde el cual usted y sus socios seran capaces de responder a la mayoria de los eventos sociales que ingresen. La informacin recolectada y mencionada en el punto 2 de este documento es proesada y usada para alimentar la bandeja de mensajes de tu usuario o de los usuarios de tu empresa. Los comentarios, mensajes y posts que se hayan registrado en tus canales sociales enlazados seran mostrados como tickets, con los cuales podras interactuar para enviar respuestas de manera manual o automatizada, entre otras opciones.",
  privacypolicy34: "4. Seguridad",
  privacypolicy35: "La seguridad de tu informacin personal y comercial es importante para nosotros. No compartimos o rentamos tu informacin con terceros. La informacin registrada solo estara disponible en nuestra plataforma para usted y los usuarios que elija.",
  privacypolicy36: "5. Solicitud de Eliminacin",
  privacypolicy37: "Estamos disponibles a cualquier solicitud de eliminacin de la informacin si fuera necesario. Para comenzar el proceso, favor de contactarnos desde el siguiente correo electrnico: laraigo@vcaperu.com. Se claro en tu solicitud y enviamos la informacin que utilizaste en nuestro proceso de suscripcin para poder identificar la informacin a eliminar. Este proceso toma de 1 a 2 dias laborales.",
  privacypolicy38: "6. Cambios a la Poltica de Privacidad",
  privacypolicy39: "Esta poltica de privacidad es efectiva a partir de 2021-11-17 y se mantendr en efecto a excepcin de que se realicen cambios en sus proviciones a futuro, los cuales tomaran efecto inmediatamente al ser incluidos en este documento.",
  privacypolicy40: "Reservamos el derecho a actualizar o cambiar nuestra Poltica de Privacidad en cualquier momento. Se recomienda revisar este documento periodicamente.",
  privacypolicy41: "Si realizamos algun cambio a esta Poltica de Privacidad, se le notificara mediante el correo electronico que registro en su suscripcin o colocando una notifiacin en nuestro sitio web.",
  privacypolicy42: "7. Contctanos",
  privacypolicy43: "Si tiene alguna pregunta sobre esta Poltica de Privacidad, favor de contactarnos al siguiente correo electrnico: laraigo@vcaperu.com",
